.todo-form {
  display: flex;
  justify-content: center;
  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.2);
  width: 90%;
  @media (max-width: 600px) {
    width: 90%;
  }
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid white;
  outline: none;
  width: 100%;
  background: transparent;
  color: #fff;

  &::placeholder {
    color: #e2e2e2;
    font-size: 1.2rem;
    // @media (max-width: 600px) {
    //   padding: 5px;
    // }
  }
}

.todo-button {
  position: relative;
  font-size: 1.2rem;
  padding: 11px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: linear-gradient(
    90deg,
    rgb(37, 117, 192) 0%,
    rgb(80, 164, 185) 100%
  );
  color: #fff;
  text-transform: capitalize;
  transition: all 0.3s ease;

  &:hover {
    background: rgb(185, 140, 183);
  }

  svg {
    font-size: 22px;
  }
}

.form-panel {
  display: flex;
  flex-direction: column;
  align-items: center;
  .sort-panel {
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

.update {
  font-size: 20px;
  transform: translate(0, 2px);
  padding: 12px;
}
